header {
  color: rgba(255, 255, 255, 0.7);
  line-height: 1;
  position: relative;
  margin-top: 0;
  margin-bottom: 0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 180px calc(var(--gutter) * 2) 0;
  height: 100vh;
}
@media (min-width: 768px) {
  header {
    padding: var(--naw-width) calc(var(--naw-width)) 0 calc(var(--naw-width));
    margin-bottom: 10vh;
  }
}

header h1 {
  font-family: "Wulkan display";
  font-weight: 500;
  margin: 0;
}

header h1 {
  font-size: 64px;
  line-height: 1;
}

@media (min-width: 768px) {
  header h1 {
    font-size: 8.0208333333vw;
    line-height: 1;
  }
}